.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

.scroll {
	overflow: auto;
}

.red-bg {
	background-color: #dc3545;
}

.white-text {
	color: white;
}

.white-color {
	background-color: white;
}

.red-text {
	color: #dc3545;
}

.red-outline {
	border-width: 3px !important;
	border-color: #dc3545 !important;
}

.inline {
	display: inline;
}

.vheight-100 {
	min-height: 100%;
	max-height: 100%;
	height: 100%;
}

.very-small-image {
	width: 13px;
	height: 13px;
}

.hand {
	cursor: pointer;
}

.no-space {
	margin: 0em;
}

.no-border {
	stroke: none !important;
}

.no-active:hover {
	cursor: auto;
	background-color: #dc3545 !important;
	box-shadow: none !important;
}

.h-300 {
	height: 300px;
}

.selected-item {
	background-color: #dc3545;
	color: white;
}

.selected-item:hover {
	background-color: #dc3545 !important;
	color: white;
}

a.bg-light:hover {
	background-color: #f5c6cb !important;
}
